<div class="hello"><div class="modern-hero"><img class="clickable" v-if="!showLineQRCode" id="logo" alt="Logo" src="../assets/logo.png" @click="showLineQRCode = true"><img class="clickable" v-else id="logo" alt="Logo" src="../assets/autobot.png" @click="showLineQRCode = false"><h2 class="modern-heading-2">🤖 自學AI</h2><p>以自學問答集為基礎，提供更即時的回答</p><p><a href="https://lin.ee/CashU46" target="_blank" rel="noopener noreferrer" style="color: white; text-decoration: underline;">將自學AI加入LINE好友</a></p><div class="ai-chat-container"><div class="ai-input-group"><input class="ai-input" autofocus type="text" placeholder="問AI關於自學的任何問題..." v-model="message" @keyup.enter="sendMessage"><button class="modern-btn modern-btn-secondary" @click="sendMessage">送出</button></div><div class="ai-result" v-if="result !== '' || (message !== '' &amp;&amp; isLoading)"><p v-if="result === '' &amp;&amp; message !== '' &amp;&amp; isLoading"> <span class="modern-loading"></span> 載入中，請稍候...</p><div v-else-if="result !== ''"><div v-html="parseResult(result)"></div><div class="mt-6"><p>您覺得這個回答怎麼樣呢？</p><div class="flex items-center justify-center gap-md mt-4"><button class="modern-btn modern-btn-secondary" @click="sendFeedback('good')">👍 很棒</button><button class="modern-btn modern-btn-outline" @click="sendFeedback('bad')" style="border-color: rgba(255,255,255,0.5); color: white;">👎 不佳</button></div></div></div></div></div></div><div class="atproto-container"><div class="atproto-hero-section"><h1 class="atproto-title">自學是什麼？</h1><p class="atproto-subtitle">正式名稱為「非學校型態實驗教育」</p><p class="atproto-description">探索自主學習的世界，了解如何開始你的自學之旅</p></div><div class="atproto-quick-nav"><div class="atproto-card atproto-card-primary" @click="scrollToSection('stereotypes')"><h3 class="atproto-card-title">刻板印象</h3><p class="atproto-card-desc">打破對自學生的常見誤解</p><span class="atproto-card-link">了解更多</span></div><div class="atproto-card atproto-card-primary" @click="scrollToSection('application')"><h3 class="atproto-card-title">申請流程</h3><p class="atproto-card-desc">完整的自學申請指南</p><span class="atproto-card-link">開始申請</span></div><div class="atproto-card atproto-card-primary" @click="scrollToSection('types')"><h3 class="atproto-card-title">自學種類</h3><p class="atproto-card-desc">個人、團體、機構自學</p><span class="atproto-card-link">探索選項</span></div></div><div class="atproto-content-section"><section class="atproto-section" id="stereotypes"><h2 class="atproto-section-title">1. 一些關於自學生的刻板印象</h2><div class="atproto-grid"><div class="atproto-feature-card"><h4 class="atproto-feature-title">都是天才？？</h4><p class="atproto-feature-desc">不一定！自學生也有各種不同的學習能力和背景</p></div><div class="atproto-feature-card"><h4 class="atproto-feature-title">家裡很有錢？？</h4><p class="atproto-feature-desc">不一定！自學可以很經濟實惠，也有很多免費資源</p></div><div class="atproto-feature-card"><h4 class="atproto-feature-title">沒有朋友？？</h4><p class="atproto-feature-desc">完全不是！自學生有豐富的社交活動和社群</p></div><div class="atproto-feature-card"><h4 class="atproto-feature-title">社交能力差？？</h4><p class="atproto-feature-desc">因人而異！自學環境反而培養出更強的溝通能力</p></div><div class="atproto-feature-card"><h4 class="atproto-feature-title">自制力很強？？</h4><p class="atproto-feature-desc">因人而異！自學過程會逐步培養自我管理能力</p></div></div></section><section class="atproto-section" id="application"><h2 class="atproto-section-title">2. 自學申請流程</h2><div class="atproto-process"><div class="atproto-process-step"><div class="atproto-step-number">1</div><h4 class="atproto-step-title">申請時間</h4><p class="atproto-step-desc">每年四月和十月</p><div class="atproto-step-detail"><a href="https://law.moj.gov.tw/LawClass/LawAll.aspx?pcode=H0070059">高級中等以下教育階段非學校型態實驗教育實施條例</a>第六條：前條申請人應填具申請書，並檢附實驗教育計畫，至遲於每年<span class="atproto-highlight">四月三十日</span>或<span class="atproto-highlight">十月三十一日</span>前提出申請。</div></div><div class="atproto-process-step"><div class="atproto-step-number">2</div><h4 class="atproto-step-title">申請材料</h4><p class="atproto-step-desc">準備必要的文件和計畫</p><div class="atproto-step-detail"><p><a href="https://www.homeschool.tw/p/blog-page.html">申請書 ➡️ 參考一週完成自學申請書</a></p><p><a href="https://youtu.be/SUoT5P8_UOI?si=zdHxoRdRoCadmYMc">實驗教育計畫書 ➡️ 參考談學習計畫-朱佳仁主講</a></p></div></div><div class="atproto-process-step"><div class="atproto-step-number">3</div><h4 class="atproto-step-title">申請人</h4><p class="atproto-step-desc">法定代理人提出申請</p><div class="atproto-step-detail"><p>國小、國中、高中職，由法定代理人提出申請。因此，家長是申請人。想要申請自學的話，需要先和家長達成共識。</p></div></div><div class="atproto-process-step"><div class="atproto-step-number">4</div><h4 class="atproto-step-title">審核與補件</h4><p class="atproto-step-desc">教育局審核流程</p><div class="atproto-step-detail"><p>• 各縣市教育局審核</p><p>• 初審不通過 ➡️ 可申請補件</p><p>• 初審通過 ➡️ 受到申請成功通知</p></div></div><div class="atproto-process-step"><div class="atproto-step-number">5</div><h4 class="atproto-step-title">延伸閱讀</h4><p class="atproto-step-desc">更多申請相關資源</p><div class="atproto-step-detail"><router-link to="/senior">「第一次申請高中自學就上手」工作坊記錄</router-link></div></div></div></section><section class="atproto-section" id="types"><h2 class="atproto-section-title">3. 自學的種類</h2><div class="atproto-grid"><div class="atproto-feature-card"><h4 class="atproto-feature-title">個人自學</h4><p class="atproto-feature-desc"><strong>與學校合作：</strong>掛籍在學校，可完全在家自學，也可部分時間在學校上課，部分時間自學</p><p class="atproto-feature-desc"><strong>教育部掛籍：</strong>在教育部註冊，完全在家自學</p></div><div class="atproto-feature-card"><h4 class="atproto-feature-title">團體自學</h4><p class="atproto-feature-desc"><strong>自學生團體：</strong>多個家庭或學生組成學習小組</p><p class="atproto-feature-desc"><strong>分攤學費：</strong>共同聘請老師或分享資源，降低成本</p></div><div class="atproto-feature-card"><h4 class="atproto-feature-title">機構自學</h4><p class="atproto-feature-desc">由專業教育機構提供課程和資源，計畫書需經過審核</p></div></div></section><section class="atproto-section"><h2 class="atproto-section-title">4. 自學生活</h2><div class="atproto-grid"><div class="atproto-feature-card"><h4 class="atproto-feature-title">成績評定</h4><p class="atproto-feature-desc"><strong>成果報告：</strong>每學年結束後兩個月內(即每年九月底前)</p><p class="atproto-feature-desc"><strong>評分標準：</strong>依自學計畫</p></div><div class="atproto-feature-card"><h4 class="atproto-feature-title">自學課程🐙</h4><p class="atproto-feature-desc"><strong>線上課程：</strong>如MOOCs、Khan Academy等平台</p><p class="atproto-feature-desc"><strong>實體課程：</strong>參加工作坊、講座或社區大學課程、到大學旁聽課程，也可自行設計課程</p></div><div class="atproto-feature-card"><h4 class="atproto-feature-title">自學資源🌵</h4><p class="atproto-feature-desc"><strong>政府補助：</strong>目前台灣高中職階段有政府補助</p><p class="atproto-feature-desc"><router-link to="/outer">線上資源：包括免費教材、教學影片和學習社群</router-link></p></div><div class="atproto-feature-card"><h4 class="atproto-feature-title">社交活動🤽‍♀️</h4><p class="atproto-feature-desc"><strong>自學生活動：</strong>聚會、讀書會或主題討論</p><p class="atproto-feature-desc"><strong>外部講座與活動：</strong>參與各種公開活動，拓展視野</p><p class="atproto-feature-desc"><strong>競賽：</strong>參加各種競賽，如科學展覽、數學競賽等</p></div></div></section><section class="atproto-section"><h2 class="atproto-section-title">5. 自學的挑戰</h2><div class="atproto-grid"><div class="atproto-feature-card"><h4 class="atproto-feature-title">撞牆期</h4><p class="atproto-feature-desc"><strong>不知道如何規畫：</strong>如果不知道自學的目標，可以參考<a href="https://www.youtube.com/watch?v=SUoT5P8_UOI&amp;list=PLebzuoh5ZI3JKOarYED1ASt2kG9g5sKFj" target="_blank" rel="noopener noreferrer">談學習計畫-朱佳仁主講</a></p><p class="atproto-feature-desc"><strong>不知道未來：</strong>如果不知道自學的未來，可以參考<a href="https://www.youtube.com/watch?v=ZPzqw3x05-g&amp;list=PLebzuoh5ZI3JKOarYED1ASt2kG9g5sKFj&amp;index=6" target="_blank" rel="noopener noreferrer">如何成為未來職場需要的自由人才-唐鳳主講</a></p></div><div class="atproto-feature-card"><h4 class="atproto-feature-title">家長的擔憂👀</h4><p class="atproto-feature-desc"><strong>不了解自學：</strong>可請家長參考<a href="https://www.alearn.org.tw/">自主學習促進會</a></p><p class="atproto-feature-desc"><strong>擔心社交問題：</strong>可請家長參考<a href="https://we.alearn.org.tw">自學2.0</a></p></div></div></section><section class="atproto-section"><h2 class="atproto-section-title">6. 自學的溝通</h2><div class="atproto-grid"><div class="atproto-feature-card"><h4 class="atproto-feature-title">與家長溝通</h4><p class="atproto-feature-desc">計劃與目標：計劃書的撰寫、家長的期望和孩子的興趣</p></div><div class="atproto-feature-card"><h4 class="atproto-feature-title">與學校溝通</h4><p class="atproto-feature-desc">合作方式：掛籍在學校，可完全在家自學，也可部分時間在學校上課，部分時間自學</p></div></div></section><section class="atproto-section"><h2 class="atproto-section-title">7. 自學後的發展</h2><div class="atproto-grid"><div class="atproto-feature-card"><h4 class="atproto-feature-title">畢業後的選擇</h4><p class="atproto-feature-desc"><strong>考大學：</strong>自學經歷在申請時是加分項</p><p class="atproto-feature-desc"><strong>工作或實習：</strong>自學培養的自主能力很受雇主歡迎</p></div><div class="atproto-feature-card"><h4 class="atproto-feature-title">自學經驗的影響</h4><p class="atproto-feature-desc"><strong>成熟度：</strong><router-link to="four">學習自律四階段🐫</router-link></p><p class="atproto-feature-desc"><strong>自我管理能力：</strong>時間管理、目標設定、自我評估</p></div></div></section><section class="atproto-section"><h2 class="atproto-section-title">問答集</h2><div class="atproto-cta"><p class="atproto-cta-text">請參考 ➡️<router-link to="faq">自學問答集</router-link></p></div><div class="atproto-divider"></div><h3 class="atproto-subsection-title">找夥伴</h3><div class="atproto-cta"><p class="atproto-cta-text">請參考 ➡️<a href="https://we.alearn.org.tw">自學2.0</a></p></div><div class="atproto-divider"></div><h3 class="atproto-subsection-title">真人諮詢</h3><div class="atproto-cta"><p class="atproto-cta-text">請用「您居住的縣市」 + 「非學校」當關鍵字，即可查詢到教育局的相關業務單位，通常都會有聯絡電話。</p></div><div class="atproto-divider"></div><h3 class="atproto-subsection-title">公共意見徵集👥</h3><div class="atproto-cta"><p class="atproto-cta-text">請上 ➡️<a href="https://polis.tw/5azfwmdsb6" target="_blank" rel="noopener noreferrer">Polis 意見徵集討論區</a></p><p class="atproto-cta-text">提出意見和對其他人的意見進行投票</p></div><div class="atproto-divider"></div><h3 class="atproto-subsection-title">相關連結</h3><div class="atproto-link-grid"><a class="atproto-link-card" href="https://law.moj.gov.tw/LawClass/LawAll.aspx?pcode=H0070059"> <span class="atproto-link-text">高級中等以下教育階段非學校型態實驗教育實施條例</span></a><a class="atproto-link-card" href="https://www.alearn.org.tw"> <span class="atproto-link-text">自主學習促進會</span></a><a class="atproto-link-card" href="https://galacticproject.notion.site/d7477db76ecd4ff3b453a39ba21d97ef"> <span class="atproto-link-text">星河計畫：自學生經驗匯流</span></a></div><div class="atproto-divider"></div><section class="atproto-section"><h3 class="atproto-subsection-title">關於我們</h3><div class="atproto-organization-card"><div class="atproto-org-header"><h4 class="atproto-org-title">本站由非營利組織自主學習促進會提供</h4></div><div class="atproto-org-content"><p class="atproto-org-desc">本會是一個非營利組織，需要您的支持。</p><div class="atproto-donation-section"><h5 class="atproto-donation-title">以愛心碼捐助本會</h5><p class="atproto-donation-desc">愛心碼取為「9806」，是因為最初成立的契機，在於1998~2006年的「台北市自主學習（中學六年一貫）實驗計畫」，本會第一代會員多是計畫中的親師生及關注計畫的學者賢達，別具意義。</p><p class="atproto-donation-desc">邀請並歡迎大家未來在開立電子發票的商家購物時，可主動向店員要求使用愛心碼，說出捐贈碼9806，或出示條碼即可。發票若中獎，將會自動捐入本會！</p><div class="atproto-donation-code"><span class="atproto-code-label">愛心碼：</span><span class="atproto-code-value">9806</span><div class="atproto-barcode-container"><img class="atproto-barcode" src="../assets/9806.png" alt="自主學習捐贈碼 9806"></div></div></div></div></div></section></section></div></div></div>